<template>
  <v-card width="500px">
    <v-card-title v-html="'Recuperacion de contraseña'" />
    <v-card-text>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        >
        <v-text-field
          v-model="email"
          label="Email"
          type="email"
          dense
          outlined
          :rules="emailRules"
          >
        </v-text-field>
        <v-btn @click="recover()" color="accent" block :loading="processing">Recuperar</v-btn>
        <v-divider class="my-2" />
        <v-btn @click="$emit('change-mode-login')" color="secondary" block>Volver</v-btn>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
import { getAuth, sendPasswordResetEmail } from 'firebase/auth'
export default {
  name: "Recuperacion",
  data () {
    return {
      email: '',
      emailRules: [
        v => !!v || 'El email es requerido',
        v => /.+@.+/.test(v) || 'El email debe ser valido'
      ],
      processing: false,
      valid: false
    }
  },
  methods: {
    async recover () {
      this.processing = true
      this.valid = await this.$refs.form.validate()
      if (this.valid) {
        try {
          const auth = getAuth()
          await sendPasswordResetEmail(auth, this.email)
          this.$toasted.success('Email con instrucciones enviado..', { icon: 'done', duration: 3000 })
        } catch (error) {
          const errorCode = error.code
          if (errorCode === 'auth/user-not-found') this.$toasted.error('Email no encontrado.', { icon: 'error', duration: 3000 })
          else this.$toasted.error('Error en recuperación de email', { icon: 'error', duration: 3000 })
        }
      }
      this.processing = false
    }
  }
}
</script>
